.home{
  
    opacity: 0.9;
    width: 100%;
    margin: auto;
       display: flex;
    flex-wrap: wrap;
    align-items:center;
    text-align: center;
    justify-content: space-between;
    height: 70vh;
}

.home-container, .header-image {
    margin-top: 40px;
}
.home .home-container h1{
    margin:0;
    margin-left: 100px;
    text-align: left;
    color: rgb(48, 45, 45);
    font-size: 40px;
  
}

.home .home-container .hello{
    font-size: 63px;
}


 .header-image{
    flex-basis: 50%;


}

.header-image img{
    width: 100%;


}

@media only screen and (max-width:500px) {

    .home{
        padding-top: 12%;
    }
    .header-image{
        flex-basis: 100%;
       
    }
    .home .home-container .hello{
        font-size: 40px;
    }
}