.navbar{
    background: #181818;
    height: 80px;
    width: 100%;
    position: fixed;
    z-index: 999;
    opacity: 0.9;
    display: flex;
    justify-content: center;
    align-items: center;


}

.navbar nav{
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.navbar nav ul {
list-style: none;
margin:0;
padding: 0;
}
.navbar nav ul li{
    display: inline-block;
    margin: 1.5vw;
}

.navbar nav ul li a{
    text-decoration: none;
    color: #fff;
    font-size: 1.5rem;
    cursor: pointer;
}

.navbar nav ul li a:hover{
    border-bottom: 5px solid rgb(218, 173, 206) ;
}

.navbar .social-icons{

    font-size: 2rem;
    margin-left: 1.5vw;
    margin-top: 10px;

}

.navbar .social-icons .soc_icon{
    margin-left: 40px;
    cursor: pointer;
    color: white;
}

@media only screen and (max-width:500px) {

    .navbar nav{
        flex-direction: column;

    }
    .navbar .social-icons{
        margin-left: 0px;
    }

    .navbar nav ul li a{
        font-size: 1rem;
    }
}