.card{
    display:flex;
    padding: 30px 0;
    border-top: 1px solid #ccc;
    align-items: center;
    justify-content: center;
}

.card-img{
 width: 30%;


}
.card-info{
  width: 40%;
    color: #555;
    margin-left: 20px;

}

.card-img img{
    width: 70%;
    border-radius: 50px;
    overflow: hidden;
  
}

.card-info h3{
font-weight: 600;
font-size: 22px;
margin: 4px 0;
 color: #333;
}



.card-tags{
    text-align: right;
    padding:5px;
}

.card-tags p{
    font-size:20px ;
    padding-left: 5px;
}

@media only screen and (max-width:500px) {

    .card{
      flex-direction: column;
      padding:10%;
    }
    
.card-img{
    width: 90%;
     
   }
   .card-info{
    width: 80%;
   margin-left: 2%;
  }
  .card-tags{
    text-align: center;

}
.card-info h3{
     font-size: 18px;
       }
    .card-info p{
         font-size: 15px;
               }
}


  