.skills{
    background-color: black;
    padding-bottom: 5%;
}

.skills p{
    color: #fff;
    padding-left: 25%;
    width: 50%;
    line-height: 2;
}

.skills-container{
    display: flex;
  margin-left: 10%;
  margin-right: 10%;
    align-items: center;
    justify-content: space-evenly;
    
}
.skills-list ul {
    color: #ffffff;
    list-style: none;
    padding: 0;
    background-color: black;
    box-shadow: 0 0 1vw 0.3vw white;
    border-radius: 9px;
    margin-bottom: 2px;
   
}
.skills-list ul li{
    display: flex;
    justify-content: center;
    font-size: 18px;
    padding: 20px;
}

.skills-icons{
    color: white;
    margin: 2%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 4rem;

}

@media only screen and (max-width:500px) {
    .skills{
        padding-top: 8%;
    }
    .skills-icons{
        font-size: 2rem;
    }
    .skills-container{
        flex-direction: column;
    }
 
}
