.pageheader-container{
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    padding-bottom:2%; 
}

.pageheader-container hr{
    position: absolute;
    left: 0;
    width: 124px;
   border: 5px solid rgb(218, 173, 206);

}

.pageheader-container h1{
    color:#555;
}

@media only screen and (max-width:500px) {

    .pageheader-container h1{
        font-size: 1.5rem;
    }
}



