.about-me{
    background-color: #181818;
    height: 30vh;
    color: white;
    padding-top: 2%;
    padding-bottom:5% ;
 
}

.about-me-container{
    display: flex;
    align-items: center;
    padding:3%
}

 .about-me-text{
margin-left: 3%;
width: 57%;
}

.about-me-button{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40%;
}
.about-me .btn{
background-color: rgb(199, 72, 199);
color: white;
padding: 18px 30px;
border-radius: 20px;
text-decoration: none;
letter-spacing: 2px;
font-weight: 600;
font-size: 18px;
}   

@media screen and (max-width:500px){
  .about-me-container{
 flex-direction: column;
 align-items: center;

  }
  .about-me-text, .about-me-button{
    width: 100%;
 

  }
  .about-me{
    width: 100%;
    margin-top: 8%;
    font-size: 0.8rem;
    padding-bottom: 8%;
  }
}